import React, {Fragment} from "react";
import {Helmet} from "react-helmet";
import Constant from "../../../../../Constant";

import WithSidebar from "../../../../../Container/WithSidebar/WithSidebar";
import AuthorInfo from "../../../../../Components/AuthorInfo/AuthorInfo";
import Swift from "../Swift";
import Image from "../../../../../Components/UI/Image/image";
import SwiftCode from "../../../../../Components/SwiftCode/SwiftCode";

const VarLetName = (props) => {
    return(
        <Fragment>
            <Helmet>
                <title>What Variable and Constant in Swift? | Swift</title>
                <meta name="description" content="Both Variable and Constant are used to store data but 
                the data asssign to variable can be change while the data assigned to constant cannot 
                be change. To create var we use the let keyword and constant, we use the const keyword."/>
                <meta name="keywords" content="variable,constant,documentation,create,iOS,swift,tutorial,reference"/>
                <meta name="author" content="Wes Chua Swee Kwang"/>
                <link href={Constant.address + "/reference/swift/varlet"} rel="canonical"/>
                <link href={Constant.address} rel="home"/>
                <meta name="robots" content="index, follow"/> 
            </Helmet>

            <WithSidebar 
                data = {Swift}
                headerTitle = "Swift Reference"
                headerURL = "/reference/swift"
                activate = {[1,1]} // Section, Row
                state = {props.state}>

            <h1 className = "subHeader">What Variable and Constant in Swift? </h1>
            <h2 className = "header">Variable, Constant and Naming Rules</h2>
            <AuthorInfo
                version = "Xcode 12.2, Swift 5.3"/>
            <ol>
                <li><a href = "/reference/swift/comments#single">Variable</a></li>
                <li><a href = "/reference/swift/comments#multi">Constant</a></li>
                <li><a href = "/reference/swift/comments#good">Naming Rules and Conversion</a></li>
            </ol>

            <p>Variables and Constants are used to store data in your computer or phone. Just like the box
              show in the image below, when these boxes store a value 1 in the each boxes but there are a different 
              between variable ans constant although both store data.</p>
            <Image
                src = "https://github.com/Sweekwang/appdevelopments.co-img-assets/blob/main/References/iOS/Swift/Variable%20Constant%20Naming/variable%20constant.png?raw=true"
                alt = "Constant and Variable in Swift"
                size = "W400"
                />
            <h3>Variable</h3>
            <p>As mention above, variable is used to store data but what is the different between variable and constant?</p>
            <p>As you see from the image above, the box in the variable does not have cap meaning you can take out the value 
               inside and change it while constant cannot.</p>
            <p>To create a variable, we use the keyword, <strong>var</strong> and we use a assignment operator (=) to 
               store the value.</p>
            <SwiftCode 
                code = {<Fragment>
                    <span className = "darkpurple">var</span> <span className = "blue">variableBox</span> = <span className = "darkblue">1</span>
                </Fragment>} />

            <p>To change the value inside the box (variable), we can use the variable name and assignment operator 
               and assign it to a new value. You do not need to use the <strong>let</strong> keyword when 
               changing to new value.</p>
            <SwiftCode 
                code = {<Fragment>
                    <span className = "blue">variableBox</span> = <span className = "darkblue">2</span>
                </Fragment>} />
            <h3>Constant</h3>
            <p>As mention above, the value in the constant cannot be change (immutable). Once you create a constant,
               the value is always fixed and sealed.</p>
            <p><strong>What is it for?</strong> and why can't I just create a variable? Constant is useful 
               and it is used as a safety feature to prevent you from accidentally changing a value that should 
               not be change.</p>
            <p>To create a constant, we use a <strong>let</strong> keyword.</p>
            <SwiftCode 
                code = {<Fragment>
                    <span className = "darkpurple">let</span> <span className = "blue">constantBox</span> = <span className = "darkblue">1</span>
                </Fragment>} />
            <p>If you try to change the <strong>value in the constant</strong>, you will get an error message.</p>
            <SwiftCode 
                code = {<Fragment>
                    <span className = "darkpurple">let</span> <span className = "blue">constantBox</span> = <span className = "darkblue">1</span>
                </Fragment>}
                result = "Left side of mutating operator isn't mutable: 'constantBox' is a 'let' constant" />

            <h3>Naming Rules </h3>
            <p>You can name anything to your variable or constant but there are some rule to follows :</p>
            <p><strong>Things you cannot do: </strong></p>
            <SwiftCode 
                    code = {<Fragment>
                         {/* eslint-disable-next-line  */}
                        <span classNam = "grey">// You cannot start the name with numbers </span> <br/>  
                        <span className = "darkpurple">let</span> <span className = "blue">1name</span> = <span className = "darkblue">1000</span> <br/>
                        {/* eslint-disable-next-line  */}
                        <span classNam = "grey">// You cannot start the name with special letter </span> <br/>  
                        <span className = "darkpurple">let</span> <span className = "blue">@name</span> = <span className = "darkblue">1000</span> <br/>
                        {/* eslint-disable-next-line  */}
                        <span classNam = "grey">// You cannot include special letter in your name</span> <br/>  
                        <span className = "darkpurple">let</span> <span className = "blue">na/me</span> = <span className = "darkblue">1000</span> <br/>
                        {/* eslint-disable-next-line  */}
                        <span classNam = "grey">// You cannot use keywoord as a name. </span> <br/>  
                        <span className = "darkpurple">let</span> <span className = "darkpurple">if</span> = <span className = "darkblue">1000</span> <br/>
                    </Fragment>} />
            <p><strong>Things you can do: </strong></p>
            <SwiftCode 
                    code = {<Fragment>
                         {/* eslint-disable-next-line  */}
                        <span classNam = "grey">// You can use small or capital letter</span> <br/>  
                        <span className = "darkpurple">let</span> <span className = "blue">NaMe</span> = <span className = "darkblue">1000</span> <br/>
                        {/* eslint-disable-next-line  */}
                        <span classNam = "grey">// You can use number inside your name but cannot start with number</span> <br/>  
                        <span className = "darkpurple">let</span> <span className = "blue">name2</span> = <span className = "darkblue">1000</span> <br/>
                        <span className = "darkpurple">let</span> <span className = "blue">na2me</span> = <span className = "darkblue">1000</span> <br/>
                        {/* eslint-disable-next-line  */}
                        <span classNam = "grey">// You can use underscore</span> <br/>  
                        <span className = "darkpurple">let</span> <span className = "blue">_name_</span> = <span className = "darkblue">1000</span> <br/>
                    </Fragment>} />
            <h3>Naming Conversion</h3>
            <p>In Swift, the commom and conversion way of naming your varibale and constant is to use <strong>
                camel case</strong>. What is Camel Case?</p>
            <p>Start your name with a lowercase letter and if the name is made up of mutiple words, join them 
               by using the word starting with a uppercase letter such as (myName).</p>
               <SwiftCode 
                    code = {<Fragment>
                        <span className = "darkpurple">let</span> <span className = "blue">myNameIs</span> = <span className = "red">"Wes"</span> <br/>
                    </Fragment>} />
            </WithSidebar>
        </Fragment>
    )
};

export default VarLetName;
